import React from 'react';
import { Pie } from '@antv/g2plot';
import { countNum } from '@/utils/index';
import styles from '../index.scss';

class HostPie extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          type: '',
          value: 27
        },
        {
          type: '',
          value: 25
        },
        {
          type: '',
          value: 18
        }
      ]
    };
    this.piePlot = null;
  }

  componentDidMount() {
    this.renderPie();
  }

  async UNSAFE_componentWillReceiveProps(nextProps) {
    console.log('nextProps', nextProps);
    // 
    const stateList = nextProps.allHost.map(item => item.state);
    // 
    const stateAndNum = countNum(stateList);
    const data = stateAndNum.map(item => {
      return {
        type: item[0] === 1 ? '' : item[0] === 0 ? '' : '',
        value: item[1]
      };
    });
    this.piePlot ? this.piePlot.changeData(data) : null;
    // await this.setState(
    //   {
    //     data
    //   },
    //   () => {
    //     console.log(this.state);
    //   }
    // );
  }

  componentWillUnmount() {
    this.piePlot.destroy();
  }

  renderPie = () => {
    this.piePlot = new Pie(document.getElementById('containers'), {
      forceFit: true,
      title: {
        visible: true,
        text: ''
      },
      description: {
        visible: true,
        text: ''
      },
      radius: 0.8,
      data: this.state.data,
      angleField: 'value',
      colorField: 'type',
      color: state => {
        return state === ''
          ? '#20a53a'
          : state === ''
          ? 'rgb(100, 118, 151)'
          : '#ff4d4f';
      },
      label: {
        visible: true,
        type: 'spider'
      }
    });
    this.piePlot.render();
  };

  render() {
    return (
      <div className={styles.hostPie}>
        <div id='containers'></div>
      </div>
    );
  }
}

export default HostPie;
