/**
 * @description modal
 */
import React, { useState } from 'react';
import {
  Form,
  Input,
  InputNumber,
  Button,
  Modal,
  Select,
  Slider,
  Spin,
  message
} from 'antd';
import axios from '@/request/axiosConfig';
import api from '@/request/api/api_host';
import api_logs from '@/request/api/api_logs';

const { Option } = Select;

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 16 }
};

// From rules
const validateMessages = {
  required: '',
  types: {
    email: 'is not validate email!',
    number: ''
  },
  number: {
    range: ' must be between ${min} and ${max}'
  }
};

// 
const sysType = [
  'Windows Server 2008 R2',
  'Windows Server 2012 R2',
  'Windows Server 2016 R2',
  'CentOS',
  'Ubuntu'
];
const opt = sysType.map(item => (
  <Option key={item} value={item}>
    {item}
  </Option>
));

// 
const ramSize = [1, 2, 4, 8, 16, 32];
const optRAM = ramSize.map(item => (
  <Option key={item} value={item}>
    {item}G
  </Option>
));

// 
const core = [1, 2, 4, 8];
const optCore = core.map(item => (
  <Option key={item} value={item}>
    {item}
  </Option>
));

export default function createHostModal(props) {
  // state
  const [loading, setLoading] = useState(false);
  // 
  const [changeValue, setChangeValue] = useState({});

  const [form] = Form.useForm();

  const valuesChange = async value => {
    console.log('value', value);
    const newValue = Object.assign(changeValue, value);
    await setChangeValue(newValue);
    console.log('changeValue', changeValue);
  };

  // 
  const onFinish = async values => {
    await setLoading(true);
    console.log(values);

    // uid
    // const { id: uid } = JSON.parse(localStorage.getItem('userInfo'));
    // values.uid = uid;
    // values.system.includes('Windows') ? (values.dDisk = undefined) : null;

    const data = {
      hostInfo: { ...changeValue },
      id: props.hid
    };
    console.log('data', data);

    // 
    const res = await axios({
      url: api.editHost,
      method: 'post',
      data
    });

    // if (res.data.success) {
    //   // 
    //   const { id: uid } = JSON.parse(localStorage.getItem('userInfo'));
    //   props.saveOperation(uid, '', JSON.stringify([res.data.id]));
    // }
    // console.log('res', res);

    setTimeout(() => {
      setLoading(false);
    }, 500);

    // Modalmessage
    props.closeModal();
    message.success(`${values.hostName}`);
    // props.getAllHost(); // 
  };

  return (
    <Modal
      title=''
      onCancel={props.closeWarnModal}
      cancelText=''
      visible={props.warnModalVisible}
      footer={null}
    >
      <Spin size='large' tip='...' spinning={loading}>
        <Form
          form={form}
          {...layout}
          name='nest-messages'
          onFinish={onFinish}
          onValuesChange={valuesChange}
          validateMessages={validateMessages}
          initialValues={{
            ramUsed: 90,
            cpuUsed: 90,
            netWidthUsed: 90,
            cDiskUsed: 90
          }}
        >
          {/* CPU */}
          <Form.Item
            name='cpuUsed'
            label='CPU'
            rules={[{ required: true }]}
          >
            <Slider
              tipFormatter={value => `${value}%`}
              min={50}
              max={100}
              tooltipPlacement='right'
            />
          </Form.Item>
          {/*  */}
          <Form.Item
            name='ramUsed'
            label=''
            rules={[{ required: true }]}
          >
            <Slider
              tipFormatter={value => `${value}%`}
              defaultValue={80}
              min={50}
              max={100}
              tooltipPlacement='right'
            />
          </Form.Item>

          {/*  */}
          <Form.Item
            name='netWidthUsed'
            label=''
            rules={[{ required: true }]}
          >
            <Slider
              tipFormatter={value => `${value}%`}
              max={100}
              tooltipPlacement='right'
              min={50}
            />
          </Form.Item>

          {/* C */}
          <Form.Item
            noStyle
            shouldUpdate={true}
            shouldUpdate={(prevValues, currentValues) => {
              return prevValues.system !== currentValues.system;
            }}
          >
            {({ getFieldValue }) => (
              <Form.Item
                name='cDiskUsed'
                label={
                  getFieldValue('system') !== 'CentOS' &&
                  getFieldValue('system') !== 'Ubuntu'
                    ? 'C'
                    : ''
                }
                rules={[{ required: true }]}
              >
                <Slider
                  tipFormatter={value => `${value}%`}
                  min={50}
                  max={100}
                  tooltipPlacement='right'
                />
              </Form.Item>
            )}
          </Form.Item>
          <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
            <Button
              style={{ borderRadius: '8px' }}
              type='primary'
              size='large'
              htmlType='submit'
            >
              
            </Button>
          </Form.Item>
        </Form>
      </Spin>
    </Modal>
  );
}
